@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", "Tahoma", sans-serif;
  font-size: 17px;
  line-height: 28px;
  font-weight: normal;
  font-style: normal;
  color: #212327;
  background-color: #ffffff; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none; }

.container {
  /*width: 1411px;*/
  margin-left: 255px;
  margin-right: 255px; }

@media (max-width: 1024px) {
  .container {
    margin: 0 25px; } }

@media (max-width: 768px) {
  .container {
    margin: 0 17px; } }

.header {
  padding: 29px 0;
  background-color: #000000; }

.header__flex-block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__logo {
  text-decoration: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.header__logo-img {
  margin-right: 17px;
  margin-top: 5px;
  width: 50px;
  height: 50px; }

.header__logo-caption {
  margin: 0;
  margin-top: 10px;
  padding: 0;
  font-family: 'Roboto Condensed', "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: calc(29.3/25);
  letter-spacing: normal;
  color: #ffffff; }

.header__logo-text {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', "Arial", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: calc(14/12);
  letter-spacing: normal;
  color: #788891; }

.header__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__item {
  margin-left: 48px; }
  .header__item--tel {
    margin-left: 85px; }
  .header__item--call {
    margin-top: 5px; }

.header__item-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 4px;
  text-decoration: none;
  font-family: 'PT Mono', "Arial", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: calc(18/16);
  color: #ffffff; }

.header__call-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 180px;
  height: 44px;
  font-family: 'Roboto Condensed', "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: calc(17/14);
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.7px;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.header__item-text {
  margin: 0;
  padding: 0;
  letter-spacing: 0.2px; }

.header__item-svg--tel {
  margin-right: 14px;
  width: 20px;
  height: 20px; }

.header__item-svg--mail {
  margin-right: 19px;
  width: 21px;
  height: 18px; }

@media (max-width: 1024px) {
  .header {
    padding: 15px 0; }
  .header__item-svg--mail {
    width: 25px;
    height: 19px; }
  .header__item-svg--mail {
    width: 24px;
    height: 24px; }
  .header__item-text {
    display: none; }
  .header__logo-img {
    margin-top: 2px;
    margin-right: 16px;
    width: 53px;
    height: 58px; }
  .header__logo-caption {
    margin-top: 11px;
    letter-spacing: normal; }
  .header__logo-text {
    margin-top: 1px;
    letter-spacing: normal; }
  .header__item-link {
    margin-top: 0; }
  .header__item--tel {
    margin-left: 33px; }
  .header__item--call {
    display: none; }
  .header__item-svg {
    margin: 0; }
    .header__item-svg--tel {
      width: 25px;
      height: 28px; }
    .header__item-svg--mail {
