      height: 20px; } }

@media (max-width: 768px) {
  .header__logo-img {
    width: 38px;
    height: 38px; }
  .header__logo-textblock {
    display: none; }
  .header__item-svg--tel {
    width: 23px;
    height: 28px; }
  .header__item--tel {
    margin-left: 23px; } }
